<template>
	<div class="backs">
	  <div class="logheader">
        <div class="return" @click="backHandle"></div>
        <span>注册账号</span>
      </div>
        <ul>
        <li>
          <span>手机号：</span>
          <input type="text" name="" v-model="LUserPhone"  @blur="checkLPhone">
        </li>
        <li>
          <span>密码：</span>
          <input type="password" name="" v-model="LUserPsd" @blur="checkLPsd">
        </li>
        <li>
          <span>确认密码：</span>
          <input type="password" name="" v-model="LUserPsdTo" @blur="checkLPsdTo">
        </li>
        <li class="yzm">
          <span>图形验证码：</span>
          <input type="text" name="" @blur="checkLpicma" v-model="picLyanzhengma">
          <span id="mo">{{checkCode}}</span>
          <input type="button" id="code" @click="createCode" v-model="checkCode"/>
        </li>
        <li>
          <span>短信验证码：</span>
          <input type="text" name="">
           <input type="button" value="获取验证码">
        </li>
      </ul>
        <div class="user_login">注册并登陆</div>
	</div>
</template>

<script>
  var code ;//在全局定义验证码
  export default {
    data () {
      return {
        LUserPhone:'',
        checkCode:'',
        picLyanzhengma:'',
        LUserPsd:'',
        LUserPsdTo:'',
      }
    },
    methods:{
      // 验证登陆手机号格式
      checkLPhone(){
        this.$checkLPhone(this.LUserPhone);
      },
      // 图片验证码
      createCode(){
        this.checkCode = this.$createCode(this.checkCode);
      },
      // 失焦验证图和密码
      checkLpicma(){
        this.$checkLpicma(this.picLyanzhengma,this.checkCode);
      },

      //验证登陆密码格式
      checkLPsd(){
        this.$checkLPsd(this.LUserPsd);
      },
      checkLPsdTo(){
        this.$checkLPsdTo(this.LUserPsdTo,this.LUserPsd);
      },

      //后退
      backHandle(){
        this.$router.back();
      },

    },
    created(){
      this.checkCode = this.$createCode(this.checkCode);
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/loging.scss"
</style>



